/* ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////// */

.container {
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}
a{
  color: white;
}

.displayContainer {
  color: white;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  max-width: 400px;
}

.imgContainer {
  max-width: 400px;
}

.profile {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}
.profileImg {
  width: 40px;
  border-radius: 20px;
  margin-right: 10px;
  border: 2px solid white;
}
.btn {
  padding: 5px;
  margin: 0px 5px;
  background: gray;
  border-radius: 10px;
}
.interaction {
  margin: 20px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}
footer {
  font-size: 1rem;
}

@media only screen and (max-width: 768px) {
  .profile {
    display: flex;
    align-items: center;
    justify-content: start;
    margin: 10px 0px;
  }
}

@media only screen and (max-width: 426px) {
  .profile {
    display: flex;
    align-items: center;
    justify-content: start;
    margin: 10px 0px;
  }
  .imgContainer {
    width: 300px;
  }
  .displayContainer{
    margin: 0px 20px;
  }
  .interaction{
    flex-direction: column;
    justify-content: start;
  }
  .btns{
    margin: 5px 0px;
    display: flex;
    align-items: center;
  }
}

@media only screen and (max-width: 320px) {
  .profile {
    font-size: small;
    display: flex;
    align-items: center;
    justify-content: start;
    margin: 10px 0px;
  }
  .profileImg{
    width: 30px;
  }
  .imgContainer {
    width: 90vw
  }
  .displayContainer{
    margin: 0px 20px;
  }
  .interaction{
    font-size: small;
    flex-direction: column;
    justify-content: start;
  }
  .btns{
    display: flex;
    align-items: center;
  }
}
